.form-horizontal {
    border: none;
    height: 100vh;
    color: white;
    background-color: black;
    >img{
        position: absolute;
        top: 8rem;
        right: 4rem;
        width: 35%;
        height: 60%;
    }

    >fieldset {
        height: 100%;
        border: none;

        >legend {
            font-size: 2rem;
        }

        >.form-group {
            width: 100%;
            padding: .5rem;
            font-size: 1.5rem;

            >.col-md-4 {
                >input {
                    width: 50%;
                    font-size: 1rem;
                    padding: .5rem;
                    border: none;
                }

                >select {
                    width: 30%;
                    font-size: 1rem;
                    padding: .5rem;
                }
                > button{
                    font-size: 1.5rem;
                    padding: .5rem;
                    color: black;
                    background-color: red;
                    border: none;
                    width: 7rem;
                    border-radius: 1rem;
                }

            }
        }
    }
}

@media screen and (max-width : 1020px) {
    .form-horizontal {
    >img{
display: none;
    }
}
}
