.form-horizontal {
  border: none;
  height: 100vh;
  color: white;
  background-color: black;
}
.form-horizontal > img {
  position: absolute;
  top: 8rem;
  right: 4rem;
  width: 35%;
  height: 60%;
}
.form-horizontal > fieldset {
  height: 100%;
  border: none;
}
.form-horizontal > fieldset > legend {
  font-size: 2rem;
}
.form-horizontal > fieldset > .form-group {
  width: 100%;
  padding: 0.5rem;
  font-size: 1.5rem;
}
.form-horizontal > fieldset > .form-group > .col-md-4 > input {
  width: 50%;
  font-size: 1rem;
  padding: 0.5rem;
  border: none;
}
.form-horizontal > fieldset > .form-group > .col-md-4 > select {
  width: 30%;
  font-size: 1rem;
  padding: 0.5rem;
}
.form-horizontal > fieldset > .form-group > .col-md-4 > button {
  font-size: 1.5rem;
  padding: 0.5rem;
  color: black;
  background-color: red;
  border: none;
  width: 7rem;
  border-radius: 1rem;
}

@media screen and (max-width: 1020px) {
  .form-horizontal > img {
    display: none;
  }
}/*# sourceMappingURL=addProC.css.map */