.fullpage {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    background-color: rgb(0, 0, 0);
    color: aliceblue;

    >.product {
        height: 100%;
        margin: 1rem;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        border: 3px solid red;
        border-radius: 1rem;

        .part1 {
            position: relative;
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;

            .dis-heart {
                // position: relative;
                top: 0;
                position: absolute;
                display: flex;
                width: 90%;
                align-items: center;
                justify-content: space-between;

                >h3 {
                    background-color: red;
                    border-radius: 1rem;
                    padding: .5rem;
                    font-size: .8rem;
                }

                >svg {
                    width: 2rem;
                    height: 2rem;
                    color: rgb(255, 0, 0);
                    // background-color: red;

                    &:hover {
                        color: rgb(141, 3, 77);
                        border-radius: none;
                    }
                }
            }

            >img {
                // position: relative;
                // height: 50%;
                width: 100%;
                border-radius: 1rem 1rem 0 0 ;
            }
        }

        .part2 {
            position: relative;
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;

            .p2content {
                width: 100%;

                >.dis-price {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    padding: 0 1rem;
                }

                >.rat-buy {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    padding: 0 1rem;


                    h3 {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;

                        >svg {
                            width: 2rem;
                            height: 2rem;
                            margin: 0 0 0 1rem;

                            &:hover {
                                color: rgb(255, 0, 0);
                                border-radius: none;
                            }
                        }

                    }


                }
            }
        }
    }
}