.fullpage {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  background-color: rgb(0, 0, 0);
  color: aliceblue;
}
.fullpage > .product {
  height: 100%;
  margin: 1rem;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 3px solid red;
  border-radius: 1rem;
}
.fullpage > .product .part1 {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.fullpage > .product .part1 .dis-heart {
  top: 0;
  position: absolute;
  display: flex;
  width: 90%;
  align-items: center;
  justify-content: space-between;
}
.fullpage > .product .part1 .dis-heart > h3 {
  background-color: red;
  border-radius: 1rem;
  padding: 0.5rem;
  font-size: 0.8rem;
}
.fullpage > .product .part1 .dis-heart > svg {
  width: 2rem;
  height: 2rem;
  color: rgb(255, 0, 0);
}
.fullpage > .product .part1 .dis-heart > svg:hover {
  color: rgb(141, 3, 77);
  border-radius: none;
}
.fullpage > .product .part1 > img {
  width: 100%;
  border-radius: 1rem 1rem 0 0;
}
.fullpage > .product .part2 {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.fullpage > .product .part2 .p2content {
  width: 100%;
}
.fullpage > .product .part2 .p2content > .dis-price {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1rem;
}
.fullpage > .product .part2 .p2content > .rat-buy {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1rem;
}
.fullpage > .product .part2 .p2content > .rat-buy h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.fullpage > .product .part2 .p2content > .rat-buy h3 > svg {
  width: 2rem;
  height: 2rem;
  margin: 0 0 0 1rem;
}
.fullpage > .product .part2 .p2content > .rat-buy h3 > svg:hover {
  color: rgb(255, 0, 0);
  border-radius: none;
}/*# sourceMappingURL=products.css.map */